.main__header{
    width: 100vw;
    height: calc(100vh - 3rem);
    display: grid;
    place-items: center;
    margin-top: 3rem;

}
.main__header-container{
    height: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    justify-content: space-between;
    grid-gap: 6rem;
    gap: 6rem;
}
.main__header-left{
    margin-top: -3rem;
}
.main__header-left h4{
    margin-bottom: 1rem;
    font-weight: 400;
    color: var(--button-color);
    font-size: 2rem;
}
.main__header-left p{
    margin: 1rem 0 3.5rem;
    font-size: 1.1rem;
}
.main__header-right{
    display: grid;
    place-items: center;
    position: relative;
}
.main__header-circle{
    width: 45rem;
    height: 45rem;
    background: linear-gradient(75deg, var(--primary-color), transparent);
    border-radius: 50%;
    position: absolute;
    transition: var(--transition);
   
}
.main__header-image{
    position: relative;   
}
.main__header-right:hover .main__header-circle{
    -webkit-filter: blur(15px);
            filter: blur(15px);
}

/* ==================waship with us =============== */
.programs{
    margin-top: 4rem;
}
.programs__wrapper{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 3rem;
    gap: 3rem;
    margin-top: 4rem;
}
.programs__program a{
    margin-top: 2.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
}
/* =============Our core values========= */
.value__container{
    display: grid;
    grid-template-columns: 38% 50%;
    grid-gap: 12%;
    gap: 12%;
}
.values__image{
    -webkit-filter: saturate(0.25);
            filter: saturate(0.25);
    transition: var(--transition);    
}
.values__image img{
    border-radius: 0 3rem  0 3rem;
}
.values__image:hover{
    -webkit-filter: saturate(1);
            filter: saturate(1);
}
.value__right > p {
    margin: 1.5rem 0 5rem;
  }
  
.value__warapper{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 3.4rem 2.4rem;
    gap: 3.4rem 2.4rem;
}
.card.values__value{
    padding-top: 3.5rem;
    text-align: left;
    position: relative;
}
.card.values__value span{
    position: absolute;
    top: -1.5rem;
}
/* =============FAQs========= */
.faqs__wrapper{
    margin-top: 4rem;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 1.5rem 5rem;
    gap: 1.5rem 5rem;
    align-items: flex-start;

}
.faq{
    background: var(--accent-color);
    padding: 2rem;
    border-radius: 1rem;
    cursor: pointer;

}
.faq div{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.faq__icon{
    background: transparent;
    color: var(--dark);
    font-size: 1.5rem;
}
.faq p{
    margin-top: 1.5rem;
}

/* =============Testimonials========= */
.testimonials{
    width: 50rem;
    margin-inline: auto;
}
.testimonails__head{
    justify-content: center;
}
.card.testimonial{
    position: relative;
    text-align: left;
    margin-top: 5rem;
}
.testimonial__avatar{
    width: 4rem;
    height: 4rem;
    border-radius: 1.5rem;
    border: 2px solid transparent;
    overflow: hidden;
    position: absolute;
    top: -2rem;
    left: calc(50% - 2rem);
    box-shadow: 0 1.5rem 2rem rgba(26, 26, 54, 0.4);
    transition: var(--transition);
}
.testimonial__avatar:hover.testimonial__avatar{
    border-radius: 50%;
    border-color: var(--accent-color);
}
.testimonial__quote{
    font-style: italic;
    margin: 1rem 0 2rem;
    color: var(--dark);
}
.card small .testimonial__title{
    margin-top: 0.3rem;
}
.testimonials__btn-container{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 2.5rem auto 0;
    display: flex;
    justify-content: center;
    align-items: center;
    grid-gap: 1rem;
    gap: 1rem;
}
.testimonials__btn{
    background: transparent;
    cursor: pointer;
}
.testimonials__btn svg{
    fill: var(--accent-color);
    font-size: 3rem;
    width: 3rem;
    height: 3rem;
}


/* =============Footer========= */
footer{
    background: var(--primary-color);
    margin-top: 7rem;
    padding-top: 7rem;
    font-size: 0.9rem;
    color: var(--dark);
}
.footer__container{
    display: grid;
    grid-template-columns: 26rem 1fr 1fr 1fr;
    grid-gap: 6rem;
    gap: 6rem;
}

.footer__container article{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    grid-gap: 0.7rem;
    gap: 0.7rem;

}
.footer__container a:hover{
    color: var(--button-color);
}
.footer__container article p{
   margin-top: 0.5rem;
}
.footer__container article h4{
    margin-bottom: 0.6rem;
    font-size: 1rem;
}
.footer__socials{
    margin-top: 1.5rem;
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    align-items: center;
}
.footer__socials a{
    background: var(--accent-color);
    padding: 0.7rem;
    border: 1px solid transparent;
    border-radius: 0.6rem;
    transition: var(--transition);

}
.footer__socials a svg{
   font-size: 1.5rem; 
}
.footer__socials a:hover{
border-color: var(--dark);
background: transparent;
}
.footer__copyright{
    text-align: center;
    padding: 1.5rem 0;
    border-top: 2px solid var(--dark);
    margin-top: 5rem;
}
  /* MEDIA QUERIES (medium screens) */
