        width: 100%;
    }
    /* FOOTER */
    footer{
        margin-top: 7rem;

    }
    .footer__container{
        grid-template-columns: 1fr;
        grid-gap: 3rem;
        gap: 3rem;
    }
    .footer__container article{
        align-items: center;
    }
    .footer__container p{
        text-align: center;
    }
    }
.about__section-image {
  border-radius: 50%;
  /* height: 45rem; */
  overflow: hidden;
  -webkit-transform: skew(15deg);
          transform: skew(15deg);
  transition: var(--transition);
}
.about__section-image:nth-child(2) {
  -webkit-transform: skew(-15deg);
          transform: skew(-15deg);
}

.about__section-image:hover {
  -webkit-transform: skew(0);
          transform: skew(0);
}
.about__section-content h1 {
  margin-bottom: 2rem;
}
.about__section-content p {
  margin-bottom: 1rem;
}
.about__story-container,
.about__mission-container {
  display: grid;
  grid-template-columns: 40% 50%;
  grid-gap: 10%;
  gap: 10%;
}
.about__vision-container {
  display: grid;
  grid-template-columns: 50% 40%;
  grid-gap: 10%;
  gap: 10%;
}
